body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: "Roboto", sans-serif;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.gitImage {
  width: 25px;
  height: 25px;
}
.card {
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  border-radius: 5px;
  background-color: white;
  width: 300px;
  height: 550px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin: 15px;
  padding: 5px;
}
.card:hover {
  box-shadow: 1px 1px 20px 0 white;
}

.fetchedImg {
  width: 150px;
  height: 150px;
}

.btnFetch {
  background: #ffffff;
  padding: 18px;
  color: #726c6c;
  border: none;
  transition: all 0.5s ease 0s;
}
.btnFetch:focus {
  outline: none;
}
.btnFetch:hover {
  color: #494949;
  cursor: pointer;
  border-radius: 10px;
  border-color: #494949;
  transition: all 0.5s ease 0s;
}

form input {
  outline: none;
}

@media (max-width: 340px) {
  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .fetchedImg {
    width: 50px;
    height: 50px;
  }
}
